// define vuex Store
import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        step: 0,
        lists: [{
            title: '',
            src: './snow/index.html',
            hidden: false
        }, {
            title: 'Service Worker ',
            src: './serviceWorker/index.html',
            hidden: false
        }, {
            title: '2',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '3',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '4',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '5',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '6',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '7',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '8',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '9',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '10',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '11',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '12',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '13',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '14',
            src: './snow/index.html',
            hidden: false
        }, {
            title: '15',
            src: './snow/index.html',
            hidden: false
        }]
    },
    mutations: {
        showTag (state, tagName) {
            state.lists.forEach((list, index) => {
                let related = list.title.indexOf(tagName) > -1;
                list.hidden = !related;
            });
        } 
    }
});

export default store;